import * as React from 'react';
import { ProductSearch } from '../ProductSearch/ProductSearch';
import ProductList from './ProductResult/ProductList';
import { LoadMore } from '../Loadmore/LoadMore';

class ProductSearchContainer extends React.Component {
    constructor() {
        super();
        this.state = {
            searchValue: '',
            isLoading: false,
            products: [],
            error: null,
            showItems: 4,
            total: null
        }
    }

    fetchProducts = async (query) => {
        this.setState({ isLoading: true })
        const apiUrl = 'https://cors-anywhere.herokuapp.com/https://skincare-api.herokuapp.com/product?q=';

        try {
            const res = await fetch(apiUrl + query);
            const data = await res.json();

            this.setState({
                products: data,
                isLoading: false,
                total: data.length,
                showItems: 4,
            })
        } catch (error) {
            this.setState({ error, isLoading: false})
        }

    }

    onSearchInputChange = (e) => {
        this.setState({ searchValue: e.target.value})
    }

    resetInput = () => {
        this.setState ({ searchValue: '' })
    }

    onSearch = (e) => {
        e.preventDefault();
        this.fetchProducts(this.state.searchValue)
        this.resetInput()
    }

    handleShowMore = () => {
        this.setState({
          showItems: 
            this.state.showItems >= this.state.totalProducts ?
              this.state.showItems : this.state.showItems + 10
        })
      }

    render() {
        const { searchValue, products, isLoading, error, total, showItems} = this.state;

        return (
            <div className="container">
                <div className="row">
                    <ProductSearch
                        searchValue={searchValue}
                        onSearch={this.onSearch} 
                        onSearchInputChange={this.onSearchInputChange}/>
                    <ProductList  
                        products={products} 
                        isLoading={isLoading}
                        errorMessage={error}
                        total={total}
                        pageSize={showItems}
                    />
                    <LoadMore 
                        products={products} 
                        isLoading={isLoading}
                        onClick={this.handleShowMore}
                    />
                </div>
            </div> 
        )
    }
}

export default ProductSearchContainer;